<template>
    <div class="container">
        <form>
            <div class="form-group row">
                <div class="col">
                    <label for="exampleInputName">Имя</label>
                    <input name="name" v-model="name" pattern="[A-Za-z]{1,32}" type="text" class="form-control"
                        id="exampleInputName" required>
                </div>

                <div class="col">
                    <label for="exampleInputSurname">Фамилия</label>
                    <input name="surname" v-model="surname" pattern="[A-Za-z ]{1,32}" type="text" class="form-control"
                        id="exampleInputSurname" required>
                </div>
            </div>

            <div class="form-group row">
                <div class="col">
                    <label for="exampleInputEmail1">Почта</label>
                    <input name="email" v-model="email" type="email" class="form-control" id="exampleInputEmail1"
                        aria-describedby="emailHelp">
                    <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone
                        else.</small>
                </div>
                <div class="col">
                    <label for="online_phone">Телефон</label>
                    <input name="phone" v-model="phone" id="online_phone" class="form-control" type="tel" maxlength="50"
                        autofocus="autofocus" required="required" value="+7"
                        pattern="\+7\s?[\(]{0,1}9[0-9]{2}[\)]{0,1}\s?\d{3}[-]{0,1}\d{2}[-]{0,1}\d{2}"
                        placeholder="+7(___)___-__-__">
                    <small id="phoneHelp" class="form-text text-muted">Например: +7(912)345-67-89</small>
                </div>
            </div>
            <div class="form-group">
                <label for="exampleInputPassword1">Password</label>
                <input name="password" v-model="password" type="password" class="form-control" id="exampleInputPassword1">
            </div>

            <div class="form-group">
                <label class="mr-sm-2 sr-only" for="inlineFormCustomSelect">Preference</label>
                <select name="role" v-model="role" class="custom-select mr-sm-2" id="inlineFormCustomSelect">
                    <option disabled value="">Выберите один из вариантов</option>
                    <option value="0">Ученик</option>
                    <option value="1">Учитель</option>
                </select>
            </div>
            <p v-on:click="registration()" class="btn btn-primary">Submit</p>
            {{success}}
            {{errors}}
        </form>
    </div>
</template>

<script>
    import axios from 'axios';

    export default {
        data() {
            return {
                errors: [],
                success: [],

                name: '',
                surname: '',
                email: '',
                phone: '',
                password: '',
                role: ''
            }
        },
        methods: {
            registration: function(){
                axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*';

                console.log('wtf')
                // axios.post("https://ululaapi.herokuapp.com/users",{},{
                // headers: {
                //     // remove headers
                //     }
                // })
                axios.post("localhost:5000/users",{
                    headers: {
                        
                    }
                })
                .then(response=>{this.success.push(response)})
                .catch(err => {this.errors.push(err)})
            }
        }
    }
</script>

