import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Courses from '../views/Courses.vue'
import MyCourses from '../views/My-Course.vue'
import Meeting from '../views/Meeting.vue'
import Account from '../views/Account.vue'
import Register from '../views/Register.vue'


Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/courses',
    name: 'Courses',
    component: Courses
  },
  {
    path: '/my-course',
    name: 'My Courses',
    component: MyCourses
  },
  {
    path: '/meet',
    name: 'Meet',
    component: Meeting
  },
  {
    path: '/account',
    name: 'Account',
    component: Account
  },
  {
    path: '/account/registration',
    name: "Registration",
    component: Register
    
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
